.subbackdrop {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: var(--modalbackground);
  opacity: 1;
  transition: opacity 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.subscribe-modal {
  position: absolute;
  top: 50%;
  left: 50%;
  width: 311px;
  height: 350px;
  background-color: var(--modalbg);
  box-shadow: 0px 2px 1px 0px rgba(0, 0, 0, 0.2),
    0px 1px 3px 0px rgba(0, 0, 0, 0.12), 0px 1px 1px 0px rgba(0, 0, 0, 0);
  border-radius: 15px;

  transform: translate(-50%, -50%) scale(1);
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
}

.subbackdrop.is-hidden .subscribe-modal {
  transform: translate(-50%, -50%) scale(0);
}

.is-hidden {
  visibility: none;
  opacity: 0;
  pointer-events: none;
}

.subscribe-modal-bg {
  background-image: url(../images/modal-bg.jpg);
  width: 311px;
  height: 169px;
  border-radius: 15px 15px 0px 0px;
}

.any {
  display: flex;
}

.subscribe-modal-header {
  color: var(--primarycolor);
  font-family: 'Montserrat';
  font-size: 20px;
  font-weight: 600;
  line-height: 24px;
  padding: 32px 24px 20px 24px;
}

.newc {
  color: var(--orange);
}

.subscribe-form-cont {
  display: inline-flex;
  padding-left: 40px;
}

.subscribe-modal-email {
  display: flex;
  gap: 8px;
  outline: none;
  border: 1.5px solid transparent;
  margin-left: -20px;
}

.subscribe-form-input {
  display: flex;
  align-items: flex-start;
  justify-content: flex-start;
  width: 263px;
  height: 45px;
  font-size: 14px;
  font-weight: 400px;
  padding: 14px 18px;
  border: 1.5px solid var(--lightgray);
  border-radius: 30px;
}

.subscibe-modal-button {
  color: var(--whitecolor);
  background-color: var(--orange);
  font-size: 14px;
  font-weight: 600;
  width: 100px;
  height: 45px;
  border: 1px solid transparent;
  border-radius: 50px;
  transition: background-color 600ms ease-in-out, color 300ms ease-in-out;
  margin-left: -105px;
}

.subscribe-modal-button:hover,
.subscribe-modal-button:focus {
  color: var(--orange);
  background-color: var(--hovercolor);
}

.subscribe-modal-close {
  position: absolute;
  top: 24px;
  right: 24px;
  padding: 0px;
  background-color: transparent;
}

.subscribe-modal-close {
  fill: currentColor;
}

.subscribe-modal-close {
  color: var(--whitecolor);
  background-color: rgb(43, 28, 17);
  border: transparent;
}
